<template>
  <div class="flex justify-around border rounded w-full p-1 bg-white">
    <button
      v-for="item in titles"
      :key="item.title"
      tabindex="0"
      :disabled="disabled"
      :style="`width: ${100 / titles.length - 1}%`"
      class="text-center shadow text-sm py-2 h-full rounded"
      :class="{'bg-primary text-white shadow': item.title === activeBtnTitle, 'text-gray-900 bg-white': item.title !== activeBtnTitle}"
      @click.prevent="setItem(item)"
    >
      {{ item.title }}
    </button>
  </div>
</template>

<script setup>
import {defineEmits, defineProps, ref, toRefs, watch} from 'vue';

const props = defineProps({
  titles: Array,
  selectedItem: {
    required: false,
  },
  disabled: {
    type: Boolean,
    default: false,
  },
});
const emit = defineEmits(['update:selected-item']);
const activeBtnTitle = ref('');
const {selectedItem, titles} = toRefs(props);

// onMounted(()=>{
//   if(typeof selectedItem.value !== 'undefined'){
//     setItem(titles.value.filter(t=>t.type === selectedItem.value)[0])
//   }
// })
watch(
  () => selectedItem.value,
  (val) => {
    if (typeof val !== 'undefined') {
      setItem(titles.value.filter((t) => t.type === val)[0]);
    }
  },
  {
    immediate: true,
  }
);

function setItem(item) {
  activeBtnTitle.value = item.title;
  emit('update:selected-item', item.type);
}
</script>

<style scoped></style>
