<template>
  <div :disabled="disabled">
    <div class="flex mb-4">
      <div class="">
        <label :for="props.id">
          <slot name="icon" />
        </label>
        <input
          :id="props.id"
          class="hidden"
          type="file"
          :multiple="true"
          accept="image/png,image/gif,image/jpeg,image/webp,application/pdf,.doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document,.csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
          @input="setInputData"
          :disabled="disabled"
        />
      </div>
    </div>
    <div class="">
      <div v-if="normalizedFiles" class="flex flex-wrap gap-4">
        <div v-for="f in normalizedFiles" :key="f.id">
          <InputFileThumbWrapper :disabled="disabled" :file-type="f.file.type" :file-source="f.thumb" :file-name="f.file.name" @delete-file="deleteSelectedFile" />
        </div>
      </div>
      <div v-if="initialFilesFromServer.length" class="flex flex-wrap gap-4">
        <div v-for="f in initialFilesFromServer" :key="f.name">
          <InputFileThumbWrapper
            :disabled="disabled"
            :is-for-download="true"
            :download-path="f.path"
            :file-type="f.type"
            :file-source="f.id"
            :file-name="f.name"
            @delete-file="deleteSelectedServerFile"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import {computed, defineEmits, defineProps, ref, toRefs, watch} from 'vue';
import InputFileThumbWrapper from './InputFileThumbWrapper';

const emit = defineEmits(['file-upload', 'update:files', 'delete-initial-file']);

const initialFilesFromServer = ref([]);
const props = defineProps({
  initialFiles: {
    type: Array,
    required: false,
    default: function () {
      return [];
    },
  },
  id: {
    required: true,
    type: String,
  },
  files: {
    required: true,
    validator: function (files) {
      return ['object'].indexOf(typeof files) !== -1;
    },
  },
  multipleFiles: {
    type: Boolean,
    default: false,
    required: false,
  },
  disabled: {
    type: Boolean,
  },
});
const {files, multipleFiles, initialFiles} = toRefs(props);

watch(
  () => initialFiles.value,
  (initialFilesFromProps) => {
    if (initialFilesFromProps.length) {
      initialFilesFromServer.value = initialFilesFromProps;
    }
  }
);

function inputFilter(newFile, oldFile) {
  newFile.file = newFile;
  newFile.id = '_' + Math.random().toString(36).substr(2, 9);
  if (newFile && !oldFile) {
    if (/(\/|^)(Thumbs\.db|desktop\.ini|\..+)$/.test(newFile.name)) {
      return;
    }
    if (/\.(php5?|html?|jsx?)$/i.test(newFile.name)) {
      return;
    }
    if (newFile && newFile.file) {
      let URL = window.URL || window.webkitURL;
      if (URL) {
        newFile.blob = URL.createObjectURL(newFile.file);
      }
      newFile.thumb = '';
      if (newFile.blob && newFile.type.substr(0, 6) === 'image/') {
        newFile.thumb = newFile.blob;
      }
    }
  }
  return newFile;
}

function setInputData({target}) {
  let fileInstance;
  if (multipleFiles.value) {
    if (files.value && files.value.length) {
      fileInstance = [...files.value, ...target.files];
    } else {
      fileInstance = [...target.files];
    }
  } else {
    fileInstance = target.files[0];
  }
  // console.log(fileInstance)
  emit('update:files', fileInstance);
}

const normalizedFiles = computed(() => {
  if (files.value && files.value.length) {
    return files.value.map((f) => inputFilter(f));
  } else if (files.value && files.value.name) {
    return [inputFilter(files.value)];
  }
  return [];
});

function deleteSelectedFile(thumb) {
  if (files.value && files.value.length) {
    emit(
      'update:files',
      files.value.filter((f) => f.thumb !== thumb)
    );
  } else if (files.value && files.value.name) {
    emit('update:files', null);
  }
}
function deleteSelectedServerFile(id) {
  initialFilesFromServer.value = initialFilesFromServer.value.filter((f) => f.id !== id);
  emit('delete-initial-file', id);
}
</script>
